<template>
  <div>
    <div id="modal1" class="modal">
      <div class="modal-content">
        <h4>Resim Gönder</h4>
        <p>
          Aşağıdan göndermek istediginiz resim dosyasını yükleyiniz...
        </p>
        <img
          height="350"
          width="100%"
          :src="currentImg ? currentImg : defaultImg"
          alt=""
        />
        <form action="#">
          <div class="file-field input-field">
            <div class="btn">
              <span>Resim</span>
              <input @change="changeImg($event)" type="file" />
            </div>
            <div class="file-path-wrapper">
              <input class="file-path validate" type="text" />
            </div>
          </div>
        </form>
      </div>
      <div class="modal-footer">
        <a @click="sendImage" href="#!" class="modal-close waves-effect waves-green btn-flat"
          >Gönder</a
        >
      </div>
    </div>
  </div>
</template>

<script>
import io from "socket.io-client";

export default {
  data() {
    return {
      currentImg: "",
      defaultImg:
        "data:image/png;base64,",
      socket: io("http://localhost:5000"),
      file: null,
    };
  },
  methods: {
    changeImg(e) {
      this.file = e.target.files[0];
      this.currentImg = URL.createObjectURL(this.file);
    },
    sendImage() {
      let username = this.$route.query.username;
      const time = new Date().getHours() + ":" + new Date().getMinutes();

      const reader = new FileReader();
      reader.onload = (e) => {
        const base64 = e.target.result.replace(/.*base64,/, "");
        this.socket.emit("image", {
          user: username,
          message: base64,
          type: "image",
          time,
          id: Math.random(),
        });
      };
      reader.readAsDataURL(this.file);
    },
  },
  created() {},
};
</script>

<style scoped>
.modal {
  width: 70% !important;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  width: 0px;
}
</style>
